import { DateTime } from "luxon";

const API_HOST = "http://localhost:1337";
const API_URL = API_HOST + "/api/";
const TAGS_API = API_URL + "/tags/";
const ARTICLES_API = (articleId) => API_URL + `articles${articleId ? "/" + articleId : ""}?populate=*`;

function filterBy(value, type = "containsi") {
  return ARTICLES_API() + `&filters[tags\][tagName][$${type}]=${value}`;
}
export function getTags(setter) {
  fetch(TAGS_API)
    .then((response) => response.json())
    .then((data) =>
      setter([
        "all",
        ...data?.data?.map((tag) => {
          return tag.tagName;
        }),
      ])
    )
    .catch((error) => console.log(error));
}

function transformDate(date) {
  const format = "dd.LL.yyyy";
  return DateTime.fromISO(date).toFormat(format);
}

export function getArticlesBy(setter, category = "all") {
  fetch(category === "all" ? ARTICLES_API() : filterBy(category))
    .then((response) => response.json())
    .then((data) =>
      setter(
        data?.data?.map((article) => {
          return {
            ...article,
            previewImage: API_HOST + article.previewImage.url,
            createdAt: transformDate(article.createdAt),
          };
        })
      )
    )
    .catch((error) => console.log(error));
}

export function getSingleArticle(setter, slug) {
  fetch(ARTICLES_API(slug))
    .then((response) => response.json())
    .then((data) =>
      setter({
        ...data.data,
        previewImage: API_HOST + data.data.previewImage.url,
        createdAt: transformDate(data.data.createdAt),
      })
    )
    .catch((error) => console.log(error));
}
